<template>
  <div v-if="orderLoading"><img src="/img/82.gif" alt="Loading" style="margin: 100px auto; display: block;" /></div>
  <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'cart'}">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Оформление заказа
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{$store.state.cartProducts.length}} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST" @submit.prevent="order">
        <div class="cart__field">
          <div class="cart__data">
            <BaseFormText title="ФИО" :error="formError.name" name="name" placeholder="Введите ваше полное имя" v-model="formData.name" />

            <BaseFormText title="Адрес доставки" :error="formError.address" name="address" placeholder="Введите ваш адрес" v-model="formData.address" />

            <BaseFormText title="Телефон" :error="formError.phone" type="tel" name="phone" placeholder="Введите ваш телефон" v-model="formData.phone" />

            <BaseFormText title="Email" :error="formError.email" type="email" name="email" placeholder="Введи ваш Email" v-model="formData.email" />

            <BaseFormTextarea title="Комментарий к заказу" :error="formError.comment" name="comments" placeholder="Ваши пожелания" v-model="formData.comment" />
          </div>

          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="0" checked="">
                  <span class="options__value">
                    Самовывоз <b>бесплатно</b>
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="delivery" value="500">
                  <span class="options__value">
                    Курьером <b>500 ₽</b>
                  </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="card">
                  <span class="options__value">
                    Картой при получении
                  </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input class="options__radio sr-only" type="radio" name="pay" value="cash">
                  <span class="options__value">
                    Наличными при получении
                  </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <div class="cart__block" v-if="$store.state.cartProducts.length">
          <ul class="cart__orders">
            <OrderItem :item="item" v-for="item in products" :key="item.product.id" />
          </ul>
          
          <div class="cart__total">
            <p>Доставка: <b>{{delivery | numberFormat}} ₽</b></p>
            <p>Итого: <b>{{$store.state.cartProducts.length}}</b> товара на сумму <b>{{ totalPrice + delivery | numberFormat }} ₽</b></p>
          </div>

          <button class="cart__button button button--primery" v-if="totalAmount">
            Оформить заказ
          </button>
        </div>
        <div class="cart__error form__error-block" v-if="formErrorMassage">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{formErrorMassage}}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import OrderItem from '@/components/OrderItem';
import BaseFormText from '@/components/BaseFormText';
import BaseFormTextarea from '@/components/BaseFormTextarea';
import axios from 'axios'
import {API_BASE_URL} from '../config'
import { mapGetters } from 'vuex';
export default {
  data() {
    return {
      delivery: 500,
      formData: {},
      formError: {},
      formErrorMassage: '',
      orderLoading: false,
    }
  },
  filters: {
    numberFormat
  },
  components: {
    OrderItem,
    BaseFormText,
    BaseFormTextarea,
  },
  computed: {
    ...mapGetters({products: 'cartDetailProducts', totalPrice: 'cartTotalPrice', totalAmount: 'cartTotalAmount'}),
  },
  methods: {
    order() {
      this.orderLoading = true;

      this.formError = {};
      this.formErrorMassage = '';
      clearTimeout();
      this.loadOrderForm = setTimeout(() => {
        axios
          .post(API_BASE_URL + '/api/orders', {
            ...this.formData
          }, {
            params: {
              userAccessKey: this.$store.state.userAccessKey
            }
          })
          .then(response => {
            this.$store.commit('resetCart');
            this.$store.commit('updateOrderInfo', response.data);
            this.$router.push({name: 'orderInfo', params: {id: response.data.id}});
          })
          .catch(error => {
            this.formError = error.response.data.error.request || {};
            this.formErrorMassage = error.response.data.error.massage;
          })
          .then(() => this.orderLoading = false);
        }, 1000);
    }
  }
}
</script>