<template>
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info"> 
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :color-id.sync="filterColorId"></ProductFilter>
      <section class="catalog">
        <div v-if="productsLoading"><img src="/img/82.gif" alt="Loading" style="margin: 100px auto 0; display: block;"></div>
        <div v-else-if="productsLoadingFailed" style="margin: 100px 0 0; text-align: center;">
          Error
          <button @click="loadingPage()">Update page</button>
        </div>
        <ProductsList v-else :products="products" />
        <BasePagination v-model="page" :count="countProducts" :pre-page="productsPrePage"></BasePagination>
      </section>

    </div>
  </main>
</template>

<script>
import ProductsList from '@/components/ProductList'
import BasePagination from '@/components/BasePagination'
import ProductFilter from '@/components/ProductFilter'
import axios from 'axios'
import {API_BASE_URL} from '../config'
export default {
  components: { 
    ProductsList, 
    BasePagination,
    ProductFilter
  },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 100000,
      filterCategoryId: 0,
      filterColorId: 0,
      page: 1,
      productsPrePage: 3,

      productsData: null,

      productsLoading: false,
      productsLoadingFailed: false,
    }
  },
  computed: {
    products() {
      return this.productsData 
            ? this.productsData.items.map(product => {
              return {
                ...product,
                img: product.image.file.url
              };
              
            })
            : [];
           
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    }
  },
  methods: {
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      clearTimeout();
      this.loadProductTimer = setTimeout(() => {
        
        axios.get(API_BASE_URL + '/api/products', {
          params: {
            page: this.page,
            limit: this.productsPrePage,
            categoryId: this.filterCategoryId,
            colorId: this.filterColorId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
          }
        })
          .then(response => this.productsData = response.data)
          .catch(() => this.productsLoadingFailed = true)
          .then(() => this.productsLoading = false);
      }, 1000);
    },
  },
  watch: {
    page() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterColorId() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  }
}
</script>

