<template>
  <div v-if="$store.state.cartLoading"><img src="/img/82.gif" alt="Loading" style="margin: 100px auto; display: block;"></div>
  <div v-else-if="$store.state.cartLoadingFailed" style="margin: 100px 0; text-align: center;">
    Error
    <button @click="loadCart">Update page</button>
  </div>
    <main class="content container" v-else>
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link class="breadcrumbs__link" :to="{name: 'main'}">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link">
            Корзина
          </a>
        </li>
      </ul>

      <h1 class="content__title">
        Корзина
      </h1>
      <span class="content__info">
        {{totalAmount}} товара
      </span>
    </div>

    <section class="cart">
      <form class="cart__form form" action="#" method="POST">
        <div class="cart__field">
          <ul class="cart__list">
            <CartItem :item="item" v-for="item in products" :key="item.product.id" />
          </ul>
        </div>

        <div class="cart__block">
          <p class="cart__desc">
            Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
          </p>
          <p class="cart__price">
            Итого: <span>{{ totalPrice | numberFormat }} ₽</span>
          </p>

          <router-link class="cart__button button button--primery" tag="button" :to="{name: 'order'}" v-if="totalAmount">
            Оформить заказ
          </router-link>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
  import numberFormat from '@/helpers/numberFormat'
  import CartItem from '@/components/CartItem';
  import { mapGetters, mapActions } from 'vuex';

  export default {
    components: {
      CartItem
    },
    filters: {
      numberFormat
    },
    computed: {
      ...mapGetters({products: 'cartDetailProducts', totalPrice: 'cartTotalPrice', totalAmount: 'cartTotalAmount'}),
    },
    methods: {
      ...mapActions(['loadCart']),
    },
    created() {
      this.loadCart();
    }
  }
</script>