<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="item.product.img" width="120" height="120" :srcset="item.product.srcset" :alt="item.product.title">
    </div>
    <h3 class="product__title">
      {{item.product.title}}
    </h3>
    <p class="product__info">
      Объем: <span>128GB</span>
    </p>
    <span class="product__code">
      Артикул: {{item.productId}}
    </span>

    <FormCounter class="product__counter form__counter" v-model.number="amount" @change-count="amount = $event" />

    <!-- <div class="product__counter form__counter">
      <button type="button" aria-label="Убрать один товар">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-minus"></use>
        </svg>
      </button>

      <input type="text" v-model.number="amount" name="count">

      <button type="button" aria-label="Добавить один товар">
        <svg width="10" height="10" fill="currentColor">
          <use xlink:href="#icon-plus"></use>
        </svg>
      </button>
    </div> -->

    <b class="product__price">
      {{ item.amount * item.product.price | numberFormat }} ₽ 
    </b>

    <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteCartProduct(item.productId)">
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
  import numberFormat from '@/helpers/numberFormat'
  import FormCounter from '@/components/FormCounter'
  import { mapActions } from 'vuex';
  export default {
    data() {
      return {
        productData: null,
        productLoading: false,
        productLoadingFailed: false,
      }
    },
    props: ['item'],
    components: {
      FormCounter
    },
    filters: {
      numberFormat
    },
    computed: {
      amount: {
        get(){
          return this.item.amount;
        },
        set(value){
          this.$store.commit('updateCartProductAmount', {productId: this.item.productId, amount: value});
        }
      },
    },
    methods: {
      ...mapActions(['deleteCartProduct']),
      loadProduct() {
        this.productLoading = true;
        this.productLoadingFailed = false;
        clearTimeout();
        this.loadProductTimer = setTimeout(() => {
          
          axios.get(API_BASE_URL + '/api/baskets/products' +this.$route.params.id)
            .then(response => this.productData = response.data)
            .catch(() => this.productLoadingFailed = true)
            .then(() => this.productLoading = false);
        }, 5000);
      },
    }
  }
</script>