<template>
  <div>
    <router-link class="catalog__pic" :to="({name: 'product', params: {id: product.id}})">
      <img :src="product.img" :srcset="product.srcset" :alt="product.title">
    </router-link>

    <h3 class="catalog__title">
      <a href="#">
        {{ product.title }}
      </a>
    </h3>

    <span class="catalog__price">
      {{ product.price | numberFormat }} ₽
    </span>

    <ul class="colors colors--black">
      <li class="colors__item" v-for="(colorList, index) in product.colors" :key="colorList.id">
        <label class="colors__label">
          <input class="colors__radio sr-only" type="radio" :value="index" v-model.number="currentColor" :id="colorList.id">
          <span class="colors__value" :style="{backgroundColor: colorList.code}">
          </span>
        </label>        
      </li>
    </ul>
  </div>
</template>

<script>
import eventBus from '@/eventBus'
import gotoPage from '@/helpers/gotoPage'
import numberFormat from '@/helpers/numberFormat'

export default {
  data() {
    return {
      currentColor: 0,
    }
  },
  props: ['product'],
  filters: {
    numberFormat
  },
  methods: {
    gotoPage
  }
}
</script>