<template>
  <div>
    <button type="button" aria-label="Убрать один товар" @click="deleteProduct">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-minus"></use>
      </svg>
    </button>

    <input type="text" :value="value"
      @input="$emit('input', $event.target.value)">

    <button type="button" aria-label="Добавить один товар" @click="addProduct">
      <svg width="12" height="12" fill="currentColor">
        <use xlink:href="#icon-plus"></use>
      </svg>
    </button>
  </div>
</template>

<style>
button{
  cursor: pointer;
}
</style>

<script>
  export default {
    props: {
      value: Number
    },
    methods: {
      deleteProduct(){
        if(this.value > 0){
          this.$emit('change-count', this.value - 1)
        }
      },
      addProduct(){
        this.$emit('change-count', this.value + 1)
      }
    }
  }
</script>